html {
	font-size: var(--text-body-default);
	font-weight: 400;
	line-height: var(--base-line-height);
	color: var(--color-text-basic);
}
html[lang=ko-KR], html[lang=ko] {
	font-family: "Pretendard", sans-serif;
}
html[lang=en], html *[lang=en] {
	font-family: "Quattrocento", sans-serif;
}

/* font */
.tit-l {
	font-size: var(--text-title-large);
}
.tit-m {
	font-size: var(--text-title-medium);
}
.tit-s {
	font-size: var(--text-title-small);
}

.txt-l {
	font-size: var(--text-body-large);
}
.txt-m {
	font-size: var(--text-body-medium);
}
.txt-s {
	font-size: var(--text-body-small);
}

/* font-style */
.bold {
	font-weight: 700;
}

/* common */
.pc-only {
	display: block;
}

.mo-only {
	display: none;
}

/* blind */
.sr-only {
	overflow: hidden;
	position: relative;
	display: block;
	width: 1px;
	height: 1px;
	clip: rect(1px, 1px, 1px, 1px);
	clip-path: inset(50%);
	border: 0;
	z-index: -1;
}

@media (max-width: 768px) {
	.pc-only {
		display: none;
	}

	.mo-only {
		display: block;
	}
}
:root {
	--color-gray-0: #fff;
	--color-gray-1: #f8f8f8;
	--color-gray-2: #bbb;
	--color-gray-3: #999;
	--color-gray-4: #666;
	--color-gray-5: #222;
	--color-gray-6: #000;
	--font-small: 16px;
	--font-medium: 20px;
	--font-large: 32px;
	--font-xlarge: 80px;
	--font-xxlarge: 120px;
	--base-line-height: 1.4;
	--base-letter-spacing: -0.25px;
	--color-black: var(--color-gray-5);
	--color-white: var(--color-gray-0);
	--color-text-bolder: var(--color-black);
	--color-text-basic: var(--color-gray-6);
	--color-text-light: var(--color-gray-3);
	--color-text-white: var(--color-white);
	--color-border-basic: var(--color-gray-2);
	--color-bg-white: var(--color-white);
	--color-bg-gray: var(--color-gray-1);
	--color-bg-gray-dark: var(--color-gray-5);
	--text-title-large: var(--font-xxlarge);
	--text-title-medium: var(--font-xlarge);
	--text-title-small: var(--font-large);
	--text-body-large: var(--font-large);
	--text-body-medium: var(--font-medium);
	--text-body-small: var(--font-small);
	--text-body-default: 10px;
	--gap-title-large-desc: 60px;
	--gap-title-medium-cont: 60px;
	--card-list-gap: 40px;
	--card-list-category-pos: 20px;
	--card-list--txt-padding-y: 30px;
	--card-list-gap-title-desc: 20px;
	--card-list-gap-desc-btn: 30px;
	--tag-list-gap: 24px;
	--section-padding-x: 200px;
	--content-width: 1440px;
	--content-padding-x: 28px;
	--content-padding-x-mo: 16px;
	--breakpoint-mobile-small: 360px;
	--breakpoint-mobile-medium: 600px;
	--breakpoint-mobile-large: 768px;
	--breakpoint-tablet: 1024px;
	--breakpoint-tablet-large: 1280px;
	--breakpoint-pc: 1920px;
}

.inner {
	margin: 0 auto;
	padding: 0 40px;
	max-width: 1680px;
	width: 100%;
}

.sec {
	position: relative;
}
.sec + .sec {
	margin-top: 150px;
}
.sec:last-of-type {
	margin-bottom: 90px;
}

.sec-tit {
	margin-bottom: 50px;
	font-family: "Quattrocento", serif;
	font-size: 60px;
	color: #000;
	text-align: center;
}

.btn-more {
	display: flex;
	align-items: center;
	margin-top: 36px;
	font-family: "Pretendard", sans-serif;
	font-size: 18px;
}
.btn-more::after {
	content: "";
	display: block;
	margin-left: 2px;
	width: 14px;
	height: 14px;
	background: url("/assets/images/common/ico_more.png") no-repeat center/cover;
}

@media (max-width: 768px) {
	.inner {
		padding: 0 20px;
	}

	.sec + .sec {
		margin-top: 80px;
	}
	.sec:last-of-type {
		margin-bottom: 60px;
	}

	.sec-tit {
		margin-bottom: 40px;
		font-size: 36px;
	}

	.btn-more {
		margin-top: 28px;
		font-size: 16px;
	}
}
.header {
	padding: 24px 0;
	background-color: #fff;
}
.header .inner {
	display: flex;
	align-items: center;
	justify-content: space-between;
}
.header .logo {
	display: block;
	width: 150px;
}
.header .logo img {
	width: 100%;
}
.header .menu .btn-menu {
	width: 28px;
	height: 28px;
	background: url("/assets/images/common/ico_menu.png") no-repeat center/cover;
}
.header .menu .menu-list {
	display: none;
	position: fixed;
	top: 0;
	right: 0;
	z-index: 100;
	background-color: #fff;
}
.header .menu .menu-list.active {
	display: block;
}
.header .menu .menu-list .menu-item strong {
	font-size: 18px;
}
@media (max-width: 768px) {
	.header {
		padding: 16px 0;
	}
	.header .logo {
		width: 100px;
	}
	.header .menu .btn-menu {
		width: 24px;
		height: 24px;
	}
}

.footer {
	padding: 90px 0;
}
.footer .inner {
	display: flex;
	flex-direction: column;
	align-items: center;
	text-align: center;
	gap: 20px;
}
.footer .logo {
	display: block;
	width: 115px;
}
.footer .logo img {
	width: 100%;
}
.footer .info-list {
	font-size: 14px;
	color: #666;
	line-height: 1.6;
}
.footer .btn-text {
	color: #000;
	text-decoration: underline;
}
@media (max-width: 768px) {
	.footer {
		padding: 40px 0;
	}
	.footer .inner {
		gap: 20px;
	}
	.footer .logo {
		display: block;
		width: 115px;
	}
	.footer .logo img {
		width: 100%;
	}
	.footer .info-list {
		font-size: 14px;
	}
}